import { ViewIcon } from '@chakra-ui/icons'
import { Box, Button, FormControl, IconButton, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, Spinner, useDisclosure, useToast } from '@chakra-ui/react'
import React, { useState } from 'react'
import { ChatState } from '../../../Context/chatProvider'
import UserBadgeItem from '../../UserAvatar/UserBadgeItem'
import axios from 'axios';
import UserListItem from '../../UserAvatar/UserListItem'

const UpdateGroupChatModal = ({fetchAgain,setFetchAgain,fetchMessages}) => {
      const { isOpen, onOpen, onClose } = useDisclosure()
      const {user,selectedChat,setSelectedChat } = ChatState()
      const [loading, setLoading] = useState(false)
      const [searchResult, setSearchResult] = useState([])
      const [groupChatName, setGroupChatName] = useState()
      const [search, setSearch] = useState("")
      const [renameLoading, setRenameLoading] = useState(false)

      const toast=useToast();

      const handleRemove=async(user1)=>{
      
    if(selectedChat.groupAdmin._id !== user._id && user1._id !== user._id)
    {
       toast({
          title: 'Only Admin can Remove someone!.',
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        return;
    }
    try {
      setLoading(true);
      const config={
      headers:{
        Authorization: `Bearer ${user.token}`,
      },
    };
 const {data} =await axios.put(`/api/chat/groupremove`,{
  chatId:selectedChat._id,
  userId:user1._id,
 },config);
      
user1._id===user._id ? setSelectedChat() :setSelectedChat(data);
 setFetchAgain(!fetchAgain);
 fetchMessages();
 setLoading(false);
    } catch (error) {
      toast({
          title: 'Error Occured.',
          description:error.response.data.message,
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        setLoading(false);
    }
      }
      const handleRenaming= async()=>{
        if(!groupChatName)return;
        try {
          setRenameLoading(true);
          const config={
      headers:{
       
        Authorization: `Bearer ${user.token}`,
      },
    };
  const {data} =await axios.put("/api/chat/rename",{
    chatId:selectedChat._id,
    chatName:groupChatName,
  },config);

  setSelectedChat(data);
  setFetchAgain(!fetchAgain);
  setRenameLoading(false);
        } catch (error) {
          toast({
          title: 'Error ocuured!.',
          description:error.response.data.message,
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        setRenameLoading(false);
        }
        setGroupChatName("");
      }

   const handleAddUser = async(user1) =>{
    console.log("AddusrtoGroup");
    console.log(selectedChat);
    if(selectedChat.users.find((u)=>u._id===user1._id))
    {
        toast({
          title: 'User Already in group!.',
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        return;
    }
    if(selectedChat.groupAdmin._id!==user._id)
    {
       toast({
          title: 'Only Admin can Add someone!.',
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        return;
    }
    try {
      setLoading(true);
      const config={
      headers:{
        Authorization: `Bearer ${user.token}`,
      },
    };
 const {data} =await axios.put(`/api/chat/groupadd`,{
  chatId:selectedChat._id,
  userId:user1._id,
 },config);
      
 setSelectedChat(data);
 setFetchAgain(!fetchAgain);
 setLoading(false);
    } catch (error) {
      toast({
          title: 'Error Occured.',
          description:error.response.data.message,
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom',
        });
        setLoading(false);
    }
   }

      const handleSearch=async(query)=>{
        setSearch(query);
        if(!query)
        {
          return;
        }
        try {
          setLoading(true);
         const config={
      headers:{
        Authorization: `Bearer ${user.token}`,
      },
    };
 const {data} =await axios.get(`api/user?search=${search}`,config);
          console.log(data);
          setLoading(false);
          setSearchResult(data);

        } catch (error) {
           toast({
          title: 'Error ocuured!.',
          description:'Failed to Load Search Result',
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom-left',
        });
        setLoading(false);
        }
      }

  return (
    <>
       <IconButton display={{base:"flex"}} icon={<ViewIcon/>} onClick={onOpen} />

      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader
          fontFamily="Work sans"
          fontSize="35px"
          justifyContent="center"
          display="flex"
          >{selectedChat.chatName}</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
           <Box w="100%" display="flex" pb={3} flexWrap="wrap">
            {selectedChat.users.map(u=>(
                <UserBadgeItem 
                key={user._id}
                user={u}
                handleFunction={()=>handleRemove(u)}
                />
            ))}
           </Box>
           <FormControl display="flex">
            <Input placeholder="Chat Name" mb={3} value={groupChatName} 
            onChange={(e)=>setGroupChatName(e.target.value)}/>
            <Button variant="solid" ml={1} isLoading={renameLoading} colorScheme='teal'
            onClick={()=>handleRenaming()}>
                Update
            </Button>
           </FormControl>
           <FormControl>
            <Input 
            placeholder='Add users to group'
            mb={1}
            onChange={(e)=>handleSearch(e.target.value)}
            />
           </FormControl>
           {
            loading ? (<Spinner size="lg" />) :(
              searchResult?.map((user)=>(
                <UserListItem 
                key={user._id}
                user={user}
                handleFunction={()=>handleAddUser(user)}/>
              ))
            )
           }
          
          </ModalBody>

          <ModalFooter>
            <Button colorScheme='red' onClick={()=>handleRemove(user)}>
              Leave Group
            </Button>
            
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default UpdateGroupChatModal
