import { Box, Button, FormControl, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, useDisclosure, useToast } from '@chakra-ui/react'
import React, { useState } from 'react'
import { ChatState } from '../../../Context/chatProvider';
import axios from 'axios';
import UserListItem from '../../UserAvatar/UserListItem';
import UserBadgeItem from '../../UserAvatar/UserBadgeItem';

const GroupChatModal = ({children}) => {
    const { isOpen, onOpen, onClose } = useDisclosure()
    const [groupChatName,setGroupChatName]=useState();
    const [selectedUser,setSelectedUser]=useState([]);
    const [search,setSearch]=useState("");
    const [searchResult,setSearchResult]=useState([]);
    const [loading,setLoading]=useState(false);
    const toast=useToast();

const {chats,setChats,user} =ChatState(); 

 const handleSearch=async (query)=>{
  setSearch(query);
  if(!query)
  {
    return;
  }
  try {
    setLoading(true);
    const config={
      headers:{
        Authorization:`Bearer ${user.token}`,
      },
    };

    const {data} =await axios.get( `/api/user?search=${search}`,config);
    console.log(data);
    setLoading(false);
    setSearchResult(data);

  } catch (error) {
      toast({
          title: 'Error Occured.',
          description: "Failed to load the search Results.",
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom-left'
        })
  }
 };
  const handleSubmit= async()=>{
    if(!groupChatName ||  !selectedUser)
    {
      toast({
          title: 'Please fill all the fields.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:'top'
        });
        return;
    }
    try {
       const config={
      headers:{
        Authorization:`Bearer ${user.token}`,
      },
    };

    const {data} =await axios.post( `/api/chat/group`,{
      name:groupChatName,
      users:JSON.stringify(selectedUser.map((u)=>u._id))
    },config);
    console.log(data);
    setChats([data,...chats]);
    onClose();
    toast({
          title: 'New Group Chat Created.',
          status: 'success',
          duration: 5000,
          isClosable: true,
          position:'bottom'
        });

    } catch (error) {
      toast({
          title: 'Failed to create the chat.',
          description:error.response.data,
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom'
        });
    }
  };

  const handleGroup=(userToAdd)=>{
    console.log(selectedUser);
    console.log(userToAdd);
    if(selectedUser.includes(userToAdd))
    {
      toast({
          title: 'User already Added.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:'top'
        });
        return;
    }
    setSelectedUser([...selectedUser,userToAdd])
  };

  const handleDelete=(delUser)=>{
    setSelectedUser([...selectedUser.filter(sel=>sel._id!==delUser._id)]);
  };

  return (
    <>
    <span onClick={onOpen}>{children}</span>
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader
          display="flex"
          fontFamily="Work sans"
          justifyContent="center"
          fontSize="35px"
          >Craete Group Chat</ModalHeader>
          <ModalCloseButton />
          <ModalBody
          display="flex"
          flexDirection="column"
          alignItems="center"
          >
            <FormControl>
                <Input placeholder='Chat Name' mb={3}
                onChange={(e)=>setGroupChatName(e.target.value)}/>
            </FormControl>
            <FormControl>
              <Input placeholder='Add Users eg: john,  vikash, amit' mb={1}
                onChange={(e)=>handleSearch(e.target.value)}/>
            </FormControl>
            <Box
            w="100%" display="flex" flexWrap="wrap"
            >
            {selectedUser.map(u=>(
              <UserBadgeItem key={user._id}
              user={u}
              handleFunction={()=>handleDelete(u)}
              />
            ))}
            </Box>
            {loading ? <div>Loading</div> :(
              searchResult?.slice(0,4).map(usr=>(
                <UserListItem
                key={usr._id}
                user={usr}
                handleFunction={()=>handleGroup(usr)}
                />
              ))
            )}
          </ModalBody>

          <ModalFooter>
            <Button colorScheme='blue'  onClick={handleSubmit}>
              Create Chat
            </Button>
            </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default GroupChatModal
