import React, { useEffect, useState } from 'react'
import { ChatState } from '../Context/chatProvider'
import { Badge, Box, Button, InputGroup, Spacer, Stack, Text, useToast } from '@chakra-ui/react'

import axios from 'axios';
import { AddIcon } from '@chakra-ui/icons';
import { getSender, getSenderFull } from '../config/ChatLogics';
import ChatLoading from './ChatLoading';
import GroupChatModal from './Authentication/miscellaneous/GroupChatModal';

const MyChats = ({fetchAgain}) => {
 const [loggedUser,setLoggedUser]=useState()
 const [isOnline, setIsOnline] = useState(false)
  
const {user,chats,setChats,selectedChat,setSelectedChat}=ChatState()
const toast=useToast()


useEffect(()=>{
 
  const fetchChats= async()=>{
  try {
    const config={
      headers:{
       
        Authorization: `Bearer ${user.token}`,
      },
    };
  const {data} =await axios.get("/api/chat",config);
  
  setChats(data);
    
   
    
  } catch (error) {
    toast({
          title: 'Error Occured.',
          description:"failed to load chat",
          status: 'error',
          duration: 5000,
          isClosable: true,
          position:'bottom-left',
        });
  }
}
  setLoggedUser(JSON.parse(localStorage.getItem("userInfo")));
  fetchChats();
},[fetchAgain])

  return (
    <Box
    display={{ base: selectedChat ? "none" :"flex",md:"flex"}}
    flexDirection="column"
    alignItems="center"
    p={3}
    bg="white"
    w={{base:"100%" ,md:"31%"}}
    borderRadius="lg"
    borderWidth="1px"
    >
          <Box
       display="flex"
       pb={3}
       px={3}
       w="100%"
       fontSize={{base:"28px",md:"30px"}}
       fontFamily="Work sans"
       justifyContent="space-between"
       alignItems="center"
       >
        My Chats
    <GroupChatModal>
      <Button
      display="flex"
      fontSize={{base:"17px",md:"10px" ,lg:"17px"}}
      rightIcon={<AddIcon/>}
      >
        New Group Chat
      </Button>
     </GroupChatModal>
      </Box>
      <Box
      display="flex"
      flexDirection="column"
      overflow="hidden"
      w="100%"
      h="100%"
      p={3}
      borderRadius="lg"
      background="#F8F8F8"
      >
        
        {chats ? (
          <Stack overflowY="scroll"> 
            {chats.map((chat)=>(
              <Box
              onClick={()=>setSelectedChat(chat)}
              cursor="pointer"
              bg={selectedChat === chat ? "#38B2AC" :"#E8E8E8"}
                  color={selectedChat === chat ? "white" :"black"}
                  px={3}
                  py={2}
                  borderRadius="lg"
                  key={chat._id}
              >
                  <InputGroup>
                <Text>
                  {!chat.isGroupChat?(getSenderFull(loggedUser,chat.users).name
                 
                  )
                  :(chat.chatName)}
                  
                 
                </Text>
                <Spacer />
                    <Text display="flex" > {getSenderFull(loggedUser,chat.users).isOnline ?  
                    <Badge  variant='solid' colorScheme='green' > Online </Badge>
   :<Badge  variant='solid' colorScheme='red' > Offline </Badge>}</Text>
                  </InputGroup>
              </Box>

            ))}
          </Stack>
        ):(
          <ChatLoading/>
        )}
      </Box>
    </Box>
  )
}

export default MyChats
