import {  Button, FormControl, FormLabel, IconButton, Input, InputGroup, InputRightElement, VStack, useToast } from '@chakra-ui/react'
import React, { useState } from 'react'
import axios from "axios";
import { useHistory } from 'react-router-dom';
import { ViewIcon, ViewOffIcon } from '@chakra-ui/icons';

const Signup = () => {
const [show ,setShow] = useState(false)
const [showP ,setShowP] = useState(false)
const [name,setName] =useState()

const [email,setEmail] =useState()
const [password,setPassword] =useState()
const [confirmPassword,setConfirmPassword] =useState()
const [pic,setPic] =useState()
const [loading,setLoading]= useState(false)
const toast=useToast()
const history=useHistory()

const handleClick=()=>setShow(!show);
const handleClickPass=()=>setShowP(!showP)
const postDetails =(pics)=>{
setLoading(true)
if(pics === undefined)
{
  toast({
    title:"Please select an Image",
    status:"warning",
    duration:5000,
    isClosable:true,
    position:"bottom",

  })
return;
}
if(pics.type === "image/jpeg" || pics.type === "image/png" )
{
  const data =new FormData();
  data.append("file",pics);
  data.append("upload_preset","Chat-App");
  
  fetch("https://api.cloudinary.com/v1_1/techsoftcrews-software-solutuion-pvt-ltd/image/upload",{
    
    method:"post",
    body:data,
  }).then((res) => res.json())
  .then((data)=>{
    setPic(data.url.toString());
    setLoading(false);
  }).catch((err)=>{
    console.log(err);
    setLoading(false);
  })
}else{
   toast({
          title: 'Please select an Image.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
        setLoading(false);
        return;
}
}
const submitHandler = async ()=>{
  setLoading(true);
  if(!name || !email || !password || !confirmPassword)
  {
    toast({
          title: 'Please Fill All the Fields.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
        setLoading(false);
        return;
  }
   if(password !== confirmPassword)
  {
    toast({
          title: 'Password do not Match.',
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
       return;
  }
  try {
    const config={
      headers:{
        "Content_type": "application/json",
      },
    };
    const {data} = await axios.post("api/user",
    {name,email,password,pic},
    config);
     toast({
          title: 'Registration Successful.',
          status: 'success',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        });
     localStorage.getItem("userInfo",JSON.stringify(data));
     setLoading(false);
     history.push("/chats");   

  } catch (error) {
    toast({
          title: 'Error Ocurred.',
          description:error.response.data.message,
          status: 'warning',
          duration: 5000,
          isClosable: true,
          position:"bottom",
        })
        setLoading(false);
  }
};

  return (
    <VStack spacing='5px' >
     
      <FormControl id="first-name" isRequired>
        <FormLabel>Name</FormLabel>
        <Input placeholder='Enter Your Name'  onChange={(e)=>setName(e.target.value)}/>
      </FormControl>
      <FormControl id="email" isRequired>
        <FormLabel>Email</FormLabel>
        <Input placeholder='Enter Your Email' onChange={(e)=>setEmail(e.target.value)}/>
      </FormControl>
      <FormControl id="password" isRequired>
        <FormLabel>Password</FormLabel>
        <InputGroup>
         <Input type={showP ? "text ":"password"} placeholder='Enter Your Password' onChange={(e)=>setPassword(e.target.value)}/>
         <InputRightElement width="4.5rem">
            <IconButton h="1.75rem" size="sm" onClick={handleClickPass} icon={showP ?<ViewIcon/>:<ViewOffIcon/>}/>
                {/* {show ? "Hide" :"Show"}
            </IconButton> */}
         </InputRightElement>
        </InputGroup>
       
      </FormControl>
      <FormControl id="confirm-password" isRequired>
        <FormLabel>Confirm Password</FormLabel>
        <InputGroup>
         <Input type={show ? "text ":"password"} placeholder='Enter Your Confirm Password' onChange={(e)=>setConfirmPassword(e.target.value)}/>
         <InputRightElement width="4.5rem">
            <IconButton h="1.75rem" size="sm" onClick={handleClick} icon={show ?<ViewIcon/>:<ViewOffIcon/>}/>
                {/* {show ? "Hide" :"Show"}
            </IconButton> */}
         </InputRightElement>
        </InputGroup>
     </FormControl>
     <FormControl>
        <FormLabel>Upload Your Picture</FormLabel>
        <Input
        type="file"
        p={1.5}
        accept="image/*"
        onChange={(e)=>postDetails(e.target.files[0])}
         />
     </FormControl>
     <Button
     colorScheme='blue'
     width="100%"
     style={{marginTop:15 }}
     onClick={submitHandler}
     isLoading={loading}
     >
        Sign Up
     </Button>
     
    </VStack>
  )
}

export default Signup
